<template>
  <div class="container">
    <form class="form-signin">
      <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input
        type="email"
        id="inputEmail"
        class="form-control my-3"
        placeholder="Email address"
        required
        autofocus
        v-model="email"
      >
      <label for="inputPassword" class="sr-only">Password</label>
      <input
        type="password"
        id="inputPassword"
        class="form-control"
        placeholder="Password"
        required
        v-model="password"
      >
      <div class="checkbox mb-3">
        <label>
          <input type="checkbox" v-model="checkboxremember" @change="Remember"> Remember me
        </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit" @click.prevent="login">Sign in</button>
      <p class="mt-5 mb-3 text-muted">&copy; 2019</p>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkboxremember: null,
      email: "",
      password: ""
    };
  },
  methods: {
    login() {
      const vm = this;
      this.$http
        .post(
          "https://www.googleapis.com/identitytoolkit/v3/relyingparty/verifyPassword?key=AIzaSyClmRSSe861d5xOGXXw-ujxGDWBNn7TSuU",
          {
            email: vm.email,
            password: vm.password,
            returnSecureToken: false
          }
        )
        .then(res => {
          localStorage.setItem("remember", res.data.email);
          this.$router.push("/home");
        })
        .catch(() => {
          alert("帳號或密碼錯誤");
        });
    },
    Remember() {
      const vm = this;
      if (vm.checkboxremember != "") {
        localStorage.setItem("remember", vm.email);
      } else {
        localStorage.setItem("remember", "");
      }
    }
  },
  created() {
    const vm = this;
    const remember = localStorage.getItem("remember");
    if (remember === "") {
      vm.checkboxremember = false;
    } else {
      vm.checkboxremember = true;
      vm.$router.push("/home");
    }
    vm.email = remember;
  }
};
</script>


